
.news-container {
  width: 40%;
  height: 282px;
  margin: 30px 20px 0;
  padding: 15px 25px;
  font-size: 1.8rem;
  border-radius: 10px;
  @include backgroundGlassEffect;
  @include animationFadeIn;
  
  &__data{
    font-size: 1.4rem;
    margin-bottom: 3px;
    letter-spacing: 2px;
  }

  &__title{
    display: block;
    margin-bottom: 10px;
    font-size: 1.6rem;
    color: $color-blue;
  }

  &__img {
    width: 50%;
    height: 180px;
    min-width: 220px;
    margin: 4px 15px 15px 0;
    float: left;
    border-radius: 10px;
  }

  &__text {
    display: inline-block;
    width: 47%;
    font-size: 1.4rem;
  }

  &__btn {
    margin-top: 20px;
  }
}